



































.quote {
  margin: 0;
  color: #999;
  padding: 20px 40px;
  font-size: 14px;
  line-height: 1.67;
  position: relative;
}

.quote:before {
  position: absolute;
  top: -10px;
  left: 5px;
  content: '“';
  font-size: 35px;
}

.quote:after {
  position: absolute;
  right: 5px;
  bottom: -20px;
  content: '”';
  font-size: 35px;
}

.article {
  margin-top: 40px;
  color: #333;
  position: relative;
}

h2 {
  font-size: 30px;
}

.title {
  font-weight: 300;
}

.author {
  font-size: 14px;
  line-height: 1.67;
}

.content {
  margin: 40px 0;
  font-size: 14px;
  line-height: 1.67;
}

.editor {
  font-size: 14px;
  line-height: 1.67;
  font-style: italic;
  margin-top: 60px;
  margin-bottom: 40px;
}



