













































h4 {
  line-height: 40px;
  font-size: 16px;
  font-weight: 600;
}

.blue-label {
  width: 60px;
  height: 28px;
  margin: 0 auto 20px auto;
  background-color: #01aef0;
  border-radius: 1px;
  box-shadow: #777 1px 1px 5px;
  text-align: center;
}

.blue-label span {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  position: relative;
}

.ask .blue-label span {
  top: -10px;
}

.ask .blue-label:before {
  content: '';
  display: block;
  position: relative;
  left: -5px;
  top: 10px;
  background-color: #01aef0;
  width: 10px;
  height: 10px;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
}

.answer .blue-label:after {
  content: '';
  display: block;
  position: relative;
  right: -55px;
  top: -20px;
  background-color: #01aef0;
  width: 10px;
  height: 10px;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
}



